<template>
<section class="wrap has_header_wrap">
  <section class="wrap_in" :class="{ unscrollable: isUnscRollable }">
	<!-- 头部标题 -->
	<com-header titles="健康资讯">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->
  	
  	<!-- 资讯 -->
  	<div class="article_con">
  		<div class="a_tabs">
  			<swiper :options="swiperOption" ref="mySwiper">
          <swiper-slide v-for="(al,index) in artLists" :key="index">
            <a :class="{cur: selected==(index+1)}" @click="tabClick(index+1)" href="javascript:;"><span>{{ al.healthAdvisoryTypeName }}</span></a>
          </swiper-slide>
        </swiper>
    		</div>
    		<!-- tab-container -->
          <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
              <!-- <mt-cell v-for="n in 10" :title="'内容 ' + n" /> -->
              <ul>
                <li
                  v-for="(alh,index) in list[0]" 
                  @click="toDetail(artLists[0].healthAdvisoryTypeName, alh, index)">
                  <!-- <div class="l_left">
                    <img src="../../../static/images/articleimg.jpg" alt="">
                  </div> -->
                  <div class="l_right">
                    <p>{{ alh.networkTitle }}</p>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
              <ul>
                <li
                  v-for="(alh,index) in list[1]" 
                  @click="toDetail(artLists[1].healthAdvisoryTypeName, alh, index)">
                  <!-- <div class="l_left">
                    <img src="../../../static/images/articleimg.jpg" alt="">
                  </div> -->
                  <div class="l_right">
                    <p>{{ alh.networkTitle }}</p>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="3">
              <ul>
                <li
                  v-for="(alh,index) in list[2]" 
                  @click="toDetail(artLists[2].healthAdvisoryTypeName, alh, index)">
                  <!-- <div class="l_left">
                    <img src="../../../static/images/articleimg.jpg" alt="">
                  </div> -->
                  <div class="l_right">
                    <p>{{ alh.networkTitle }}</p>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="5">
              <ul>
                <li
                  v-for="(alh,index) in list[3]" 
                  @click="toDetail(artLists[3].healthAdvisoryTypeName, alh, index)">
                  <!-- <div class="l_left">
                    <img src="../../../static/images/articleimg.jpg" alt="">
                  </div> -->
                  <div class="l_right">
                    <p>{{ alh.networkTitle }}</p>
                  </div>
                </li>
              </ul>
            </mt-tab-container-item>
          </mt-tab-container>
  	</div>
  	<!-- 资讯 end -->
  </section>
	<!-- 资讯详情页 -->
	<transition name="fade">
		<router-view v-on:changeClass="changeClassOne" />
	</transition>
	<!-- 资讯详情页 end -->
</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle'
import {TabContainer, TabContainerItem, Cell, Toast} from 'mint-ui'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'mint-ui/lib/style.css'
	
export default {
	name: 'Article',
	data (){
		return {
      isUnscRollable: false,//是否禁止页面滚动
      backParentPage: false, //是否回到父页面
			selected: '1',
      artLists: [],//文章数据
      list:[],//二级文章
	    swiperOption: {
	       // notNextTick: true,
	       autoplay: 0,
	       slidesPerView: 4.5,
	       setWrapperSize :true,
	       slideToClickedSlide: true, //点击slide会过渡到这个slide。
	    },
		}
	},
	components: {
		  comHeaderTitle,
	    swiper,
	    swiperSlide,
	    'mt-tab-container': TabContainer,
	    'mt-tab-container-item': TabContainerItem,
	    'mt-cell': Cell,
	},
  updated (){
    console.log(this.backParentPage)
  },
  mounted () {
    // this.$emit('changeClass',false);
  },
  destroyed (){
    // this.$emit('changeClass',true);
  },
  created (){
    const _self = this;

    this.$ajax({
      url: _self.webRoot + '/healthAdvisoryType/getHealthAdvisoryTypeList'
    })
    .then(function(res){
      // console.log(res)
      if( res.data.success ){
        if( res.data.data.length <= 0 ){
          _self.$options.methods.showToast('暂无文章！');
          return false;
        }
        _self.artLists = res.data.data;
        for (var i = 0; i < res.data.data.length; i++) {
          _self.list.push(res.data.data[i].healthAdvisoryList)
        }
      }else{
        _self.$options.methods.showToast('获取数据失败！');
      }
    })
    .catch(function(err){
      console.log(err)
    })
  },
	methods: {
		//切换TAB
		tabClick ( index ){
			this.selected = String(index);
		},
		// 进入文章详情
		toDetail ( typeName, item, index ){
      // this.$router.push({ path: '/article/detail', query: { 'type': type } });
      this.$router.push({ path: '/article/detail', query: { 'typeName': typeName, aid: item.healthAdvisoryID } });
		},

    //公共
    // 判断是否返回父页面
      changeClassOne ( isChildDestroyed ){
        if( isChildDestroyed ){ 
          //退出子页面
          this.backParentPage = true; //返回首页
          this.isUnscRollable = false;
        }else{
          //没退出子页面
          this.backParentPage = false; // 没返回首页
          this.isUnscRollable = true;
        }
      },
    //显示提示信息
    showToast( msg, time ){
      var time = time ? time : 2000;

      Toast({
        message: msg,
        position: 'center',
        duration: time
      });     
    },
    
	},
}
</script>

<style scoped>
	@import './Css/article.css';
</style>


